import './header.scss';
import 'slick-carousel';

export default async () => {
	const $searchBar = $( '.header__search' );

	$( document ).mouseup( ( e ) => {
		if ( !$searchBar.is( e.target ) && $searchBar.has( e.target ).length === 0 ) {
			$searchBar.removeClass( 'is-active' );
		}
	} );

	$( '.header__search-icon' ).on( 'click', ( e ) => {
		e.preventDefault();
		$searchBar.toggleClass( 'is-active' );
	} );

	$( '.header__menu > ul > li > ul, .header__menu > ul > li > ul > li > ul' ).parent( 'li' ).addClass( 'has-children' );

	$( '.header__menu > ul > .has-children > a' ).append().after( '<span class=\'header__menu-arrow js-menu-toggle\'><i class=\'icon-angle-right\'></i></span>' );

	$( '.js-menu-toggle' ).click( function ( e ) {
		e.preventDefault();

		let $this = $( this );

		if ( $this.hasClass( 'is-active' ) ) {
			$this.removeClass( 'is-active' );
			$this.next().slideUp( 350 );
		} else {
			$this.parent().parent().find( '.js-menu-toggle' ).removeClass( 'is-active' );
			$this.parent().parent().find( '.has-children ul' ).slideUp( 350 );
			$this.toggleClass( 'is-active' );
			$this.next().slideToggle( 350 );
		}
	} );

	$( '.js-add-card' ).click( function ( e ) {
		e.preventDefault();

		$( '.single_add_to_cart_button' ).trigger( 'click' );
	} );

	const $menuBar = $( '.header' );

	$( document ).mouseup( ( e ) => {
		if ( !$menuBar.is( e.target ) && $menuBar.has( e.target ).length === 0 ) {
			$menuBar.removeClass( 'open-menu' );
			$( '.header__toggle' ).removeClass( 'is-active' );
		}
	} );

	$( '.header__toggle' ).click( function () {
		$menuBar.toggleClass( 'open-menu' );
		$( this ).toggleClass( 'is-active' );
	} );

	function stickyRelocate () {
		let windowTop = $( window ).scrollTop();
		let divTop = $( '.sticky-anchor' ).offset().top;

		if ( windowTop > divTop ) {
			$( '.sticky-topbar' ).addClass( 'sticky' );
		} else {
			$( '.sticky-topbar' ).removeClass( 'sticky' );
		}
	}

	$( function () {
		$( window ).scroll( stickyRelocate );
		stickyRelocate();
	} );

	$( window ).scroll( function () {
		if ( $( window ).scrollTop() >= 45 ) {
			$( '.header__sticky' ).addClass( 'sticky' );
		} else {
			$( '.header__sticky' ).removeClass( 'sticky' );
		}
	} );

	$( function () {
		$( '.js-scroll-down' ).on( 'click', function () {
			let target = $( this.hash );

			target = target.length ? target : $( `[name=${this.hash.substr( 1 )}]` );
			if ( target.length ) {
				$( 'html,body' ).animate( {
					scrollTop: target.offset().top - 140,
				}, 1000 );

				return false;
			}
		} );
	} );

	$( function () {
		$( '.header__menu-list' ).slick( {
			rows: 0,
			slidesToShow: 6,
			slidesToScroll: 6,
			dots: false,
			variableWidth: true,
			infinite: false,
			swipeToSlide: true,
			prevArrow: '<button class="slick-prev" aria-label="Previous" type="button"><i class="icon-angle-right"></i></button>',
			nextArrow: '<button class="slick-next" aria-label="Next" type="button"><i class="icon-angle-right"></i></button>',
			mobileFirst: false,
			responsive: [ {
				breakpoint: 768,
				settings: 'unslick',
			} ],
		} );

		$( window ).on( 'resize', function () {
			$( '.header__menu-list' ).slick( 'resize' );
		} );
	} );
};
